<template>
  <div id="app">
    <c-header></c-header>
    <router-view></router-view>
    <video id="music-box" style="display: none;" loop controls autoplay="true" name="media" :src="source"></video>
  </div>
</template>

<script>
import cHeader from '@/components/common/header'
export default {
  name: 'app',
  components: {
    cHeader
  },
  computed: {
    source() {
      return this.$store.state.source
    }
  }
}
</script>

<style>
@import 'assets/typo.css';
@import 'assets/app.css';
</style>



