import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)

export default new vuex.Store({
  state:{
    source: 'http://dl.stream.qqmusic.qq.com/C100003Gqgy22qoF7T.m4a?vkey=3AA567FAE08F0E06D1C59A877BB896150F7AE3A5575E7FE0C0F3E4E16FF3649CC6237557272D9F9AA1051CB83E3D733572A37CB8BCBF3059'
  },
  mutations:{
    switch_source(state, data){
      state.source = data
    }
  },
  actions:{
    switch_source(context, data){
      context.commit('switch_source', data)
    }
  }
})


