<template>
  <div class="nav serif">
	  <div class="search">
		  <input placeholder="搜索..." class="search-input" v-model="key" @input="search">
		  <ul class="search-complete">
				<li v-for="item in searchList" @click="turnArticle(item)" class="pointer">{{item.name}}</li>
			</ul>
	  </div>
	  <a href="#/" title="文章归档" class="nav-item"> 
	  <strong>Confetti7</strong></a>
	  <a href="#/" class="nav-item">关于</a>
	  <a href="#/article/archive" class="nav-item">归档</a>
  </div>
</template>
<style lang="less" scoped>
	.search-complete {
		opacity: 0;
	}
	.search-input:focus~.search-complete{
		opacity: 1;
	}
</style>
<script>
import articleList from '@/components/article/list.json'
export default {
	data () {
		return {
			key: '',
			searchList: []
		}
	},
	methods: {
		search () {
			// 查询列表
			this.searchList = []
			articleList.forEach((item) => {
				if (this.key && item.name.indexOf(this.key) > -1) {
					this.searchList.push(item)
				}
			})
			if (this.searchList.length < 1) {
				this.searchList.push({name: 'The article was not found!'})
			}
		},
		turnArticle (item) {
			if (item.name !== 'The article was not found!') {
				window.location.href = '#/article/' + item.fileName
			}
		}
	}
}
</script>







