<template>
  <div>
    {{index}}. <a :href="href">{{name}}</a> <small>{{time}}</small>
    <p class="typo-small">{{memo}}</p>
  </div>
</template>
<script>
export default {
  props: {
    index: '',
    href: '',
    name: '',
    time: '',
    memo: ''
  }
}
</script>




