<template>
	<div class="container typo">
    <h1 class="logo">Confetti7 的私人博客</h1>
    <p>一些成长的痕迹，会偶尔在这里写下来。<br>
    关于我，目前作为一名工程师，在用友网络处理一些关于前端的事情。<br>
    下面是最近写的一些文章：
    </p>
    <hr>
    <div style="position: relative;">
      <abstract v-for="(item, index) in articleList" :key="index" :index="index + 1" :href="item.href" :name="item.name" :time="item.time"></abstract>
      <div class="robot-box">
        <robot></robot>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
  .robot-box {
    display: inline-block;
    position: absolute;
    top: 30px;
    right: 0;
  }
</style>
<script>
import abstract from '@/components/common/abstract'
import robot from '@/components/enjoyable/robot'
import articleList from '@/components/article/list.json'
export default {
  components: {
    robot,
    abstract
  },
  data () {
    return {
      articleList: articleList
    }
  }
}
</script>




