<template>
  <div class="container typo">
    <h1 class="logo">往忆云</h1>
    <!-- <particle></particle> -->
    <div>
      <Input v-model="name" placeholder="Enter something..." icon="ios-pulse" style="float:right;width: 250px;margin-bottom:10px;" @on-click="getListByName" @on-enter="getListByName"></Input>
      <table class="music-list-box">
        <thead>
          <tr>
            <td v-for="item in theadConfig" v-bind:style="{width: item.width}">{{item.name}}</td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in musicList">
            <td>
              <div class="music-title">{{item.title}}</div>
              <div class="todo-list">
                <span v-show="source!==item.source" class="todo play" @click="play(item)"></span>
                <span v-show="source===item.source" class="todo pause" @click="pause(item)"></span>
                <span class="todo download" @click="download(item)"></span>
              </div>
            </td>
            <td>{{item.singer[0].title}}</td>
            <td>{{item.album.title}}</td>
            <td>{{item.title}}</td>
          </tr>
        </tbody>
      </table>
      <Page id="page" :total="40" size="small" show-total show-sizer @on-change="handlePageIndexChange" @on-page-size-change="handlePageSizeChange"></Page>
    </div>
  </div>
</template>
<style lang="less" scoped>
.music-list-box {
  width: 100%;
  border: none;
  table-layout: fixed;
  thead {
    tr {
      background-color: #fbfbfd;
    } 
  }
  tbody {
    tr:nth-child(even) {
      background-color: #fbfbfd;
    }
  }
  tr {
    height: auto;
    &:hover {
      .todo-list {
        opacity: 1;
      }
    }
    th, td {
      white-space: nowrap;
      overflow-x: hidden;
      text-overflow: ellipsis;
      border: none;
      .music-title {
        display: inline-block;
        width: 120px;
        padding-top: 4px;
        white-space: nowrap;
        overflow-x: hidden;
        text-overflow: ellipsis;
      }
      .todo-list {
        height: 36px;
        float: right;
        opacity: 0;
        .todo {
          cursor: pointer;
          display: inline-block;
          width: 36px;
          height: 36px;
          background-image: url(/static/img/article/icon_list_menu.png);
          background-repeat: no-repeat;
        }
        .play {
          &:hover {
            background-position: -40px 0;
          }
        }
        .pause {
          background-position: 0 -200px;
          &:hover {
            background-position: -40px -200px;
          }
        }
        .download {
          background-position: 0 -120px;
          &:hover {
            background-position: -40px -120px;
          }
        }
      }
    }
  }
}
.limit {
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}
</style>
<style lang="less">
#page {
  float: right;
  .ivu-select-dropdown-list{
    margin-left: 0;
    margin-bottom: 0;
  }
  a {
    border: none;
  }
}
.ivu-icon-ios-pulse {
  cursor: pointer;
}
</style>
<script>
import $ from 'jquery'
import jsonp from 'jsonp'
// import particle from '@/components/enjoyable/particle'
export default {
  data () {
    return {
      theadConfig: [
        {
          name: '歌曲',
          width: '30%'
        },
        {
          name: '歌手',
          width: '23.3%'
        },
        {
          name: '专辑',
          width: '23.3%'
        },
        {
          name: '时长',
          width: '23.3%'
        }
      ],
      musicList: [],
      name: '男孩',
      pageIndex: 1,
      pageSize: 20
    }
  },
  computed: {
    source() {
      return this.$store.state.source
    }
  },
  mounted () {
    this.getListByName()
  },
  methods: {
    getListByName () {
      let url = `https://c.y.qq.com/soso/fcgi-bin/client_search_cp?ct=24&qqmusic_ver=1298&new_json=1&remoteplace=txt.yqq.song&searchid=61954838856579256&t=0&aggr=1&cr=1&catZhida=1&lossless=0&flag_qc=0&p=${this.pageIndex}&n=${this.pageSize}&w=${this.name}&g_tk=5381&jsonpCallback=MusicJsonCallback08522478043023662&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0`
      jsonp(url, {name: 'MusicJsonCallback08522478043023662'}, (err, data) => {
        data = data.data.song
        data.list.forEach((item) => {
          item.source = ''
        })
        this.musicList = data.list
      })
    },
    handlePageIndexChange (index) {
      this.pageIndex = index
      this.getListByName()
    },
    handlePageSizeChange (size) {
      this.pageSize = size
      this.getListByName()
    },
    getMusic (item, songmid, filename, action) {
      let url = `https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg?g_tk=872989112&jsonpCallback=MusicJsonCallback06459212607938936&loginUin=11297258&hostUin=0&format=json&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&cid=205361747&callback=MusicJsonCallback06459212607938936&uin=11297258&songmid=${songmid}&filename=${filename}&guid=9136027940`
      jsonp(url, {name: 'MusicJsonCallback06459212607938936'}, (err, data) => {
        let vkey = data.data.items[0].vkey
        item.source = `http://dl.stream.qqmusic.qq.com/${filename}?vkey=${vkey}&fromtag=66`
        switch (action) {
          case 'play':
          this.$store.dispatch('switch_source', item.source)
          let myVideo = document.getElementById("music-box")
          myVideo.play()
          break
          case 'download':
          downloadFile(item.source, item.title)
          break
        }
      })
    },
    play (item) {
      this.action(item, 'play')
    },
    pause (item) {
      item.source = ''
      let myVideo = document.getElementById("music-box")
      myVideo.pause()
    },
    download (item) {
      this.action(item, 'download')
    },
    action (item, type) {
      let url = `https://c.y.qq.com/v8/fcg-bin/fcg_play_single_song.fcg?songid=${item.id}&tpl=yqq_song_detail&format=jsonp&callback=getOneSongInfoCallback `
      jsonp(url, {name: 'getOneSongInfoCallback'}, (err, data) => {
        let songmid = item.mid
        let filename = data.url[item.id].split('/')[1].split('?')[0]
        this.getMusic(item, songmid, filename, type)
      })
    }
  }
}
function downloadFile(url, filename) {
  var a = document.createElement('a')
  a.href = url
  a.download = filename
  document.body.appendChild(a)
  a.click()
  document.body.removeChild(a)
}
</script>

  


