<template>
  <div class="container typo">
    <h1 class="logo">文章归档</h1>
    <abstract v-for="(item, index) in articleList" :key="index" :index="index + 1" :href="item.href" :name="item.name" :time="item.time" :memo="item.memo"></abstract>
  </div>
</template>
<script>
import abstract from '@/components/common/abstract'
import articleList from './list.json'
export default {
  components: {
    abstract
  },
  data () {
    return {
      articleList: articleList
    }
  }
}
</script>




