<template>
  <div>
    <!--动态绑定组件-->
    <component v-bind:is="article">
    </component>
  </div>
</template>
<script>
  import editor from '@/components/common/editor'
  import archive from '@/components/article/archive'
  import article1 from '@/components/article/1'
  import article2 from '@/components/article/2'
  import article3 from '@/components/article/3'
  import article4 from '@/components/article/4'
  import {formatTree} from '@/util/common'
  export default {
    data () {
      return {
        article: 'editor'
      }
    },
    components: {
      editor,
      archive,
      article1,
      article2,
      article3,
      article4
    },
    mounted () {
      this.turnArticle()
    },
    methods: {
      turnArticle () {
        let href = window.location.href
        if (href.indexOf('article/') > -1) {
          if (href.indexOf('archive') > -1) {
            this.article = 'archive'
            return true
          }
          let index = href.split('article/')[1]
          this.article = 'article' + index
        } else {
          this.article = 'editor'
        }
      }
    },
    watch:{
      $route() {
        this.turnArticle()
      }
    }
  }
</script>



